body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  background: -webkit-linear-gradient(right, rgba(7,27,82,1) 0%, rgba(0,128,128,1) 100%);
  background: linear-gradient(to left, rgba(7,27,82,1) 0%, rgba(0,128,128,1) 100%);
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

/* #### Generated By: http://www.cufonfonts.com #### */

@font-face {
    font-family: 'Pavanam Regular';
    font-style: normal;
    font-weight: normal;
    src: local('Pavanam Regular'), url(/static/media/Pavanam-Regular.e2bc93ef.woff) format('woff');
    }

h1 {
    font-family: 'Pavanam Regular';
    font-weight: 400;
    color: #0ccac4;
}
